a {
  text-decoration: none;
}
a:active, a:hover {
  outline: 0;
}

ul,
li {
  list-style: none;
  list-style-type: none;
}

h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
  font-weight: normal;
}

html,
*,
*:before,
*:after {
  box-sizing: border-box;
}

:focus {
  outline: 0;
}

img,
audio,
video {
  max-width: 100%;
  height: auto;
}

audio,
canvas,
iframe,
video,
img,
svg {
  vertical-align: middle;
}

iframe {
  border: 0;
}

textarea {
  resize: none;
  overflow: auto;
  vertical-align: top;
  box-shadow: none;
  -webkit-box-shadow: none;
  -moz-box-shadow: none;
}

input,
textarea,
select,
button {
  margin: 0;
  padding: 0;
  font-size: 100%;
  background-color: transparent;
  outline: none;
  border: none;
  cursor: pointer;
  appearance: none;
}

button,
input {
  line-height: normal;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

td,
th {
  padding: 0;
  text-align: left;
}

:root {
  --dark-color: #000000;
  --white-color: #ffffff;
  --accent-color: #fe6d35;
  --black-color: #171717;
  --gray-color: #808080;
  --bg-color: #eff1f3;
  --form-bg: #f0f0f0;
  --gradient-color: linear-gradient(45deg, #4481eb 0%, #04befe 100%);
  --underline-intrinsic-width: 8;
  --underline-width: 12;
  --underline-color: #fe6d35;
  --underline-cap-width: 4px;
  --underline-offset-y: 5px;
  --underline-padding-x: 0.12em;
  --container: 1310px;
  --indent: 10px;
  --font-family: "Montserrat", sans-serif;
  --font-weight: 400;
  --font-size: 16px;
  --font-height: 1.5;
}

.theme-button {
  display: block;
  width: 100%;
  max-width: max-content;
  padding: 6px 24px;
  font-family: "Montserrat", sans-serif;
  font-weight: 500;
  font-size: 16px;
  text-decoration: none;
  color: #222222;
  background-color: red;
  border: 1px solid transparent;
  border-radius: 6px;
}

.block-title {
  display: block;
  width: 100%;
}

.h2 {
  position: relative;
  display: inline;
  font-family: var(--font-family);
  font-weight: 700;
  font-size: 28px;
  line-height: 1.4;
  --underline-width-scale: calc(var(--underline-width) / var(--underline-intrinsic-width));
  padding: 0 calc(var(--underline-padding-x) + var(--underline-cap-width) * var(--underline-width-scale));
  box-decoration-break: clone;
  background-repeat: no-repeat;
  background-image: linear-gradient(180deg, var(--underline-color), var(--underline-color));
  background-position-x: calc(var(--underline-cap-width) * var(--underline-width-scale)), 0, 100%;
  background-position-y: calc(100% - var(--underline-offset-y) * -1);
  background-size: calc(100% - var(--underline-cap-width) * var(--underline-width-scale) * 2) calc(var(--underline-width) * 1px), auto calc(var(--underline-width) * 1px), auto calc(var(--underline-width) * 1px);
}

.advanced-block-preview {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: stretch;
  width: 100%;
  padding: 40px 24px;
  container: main-container/inline-size;
}
.advanced-block-preview .mgu-main__bg {
  position: absolute;
  top: 0;
  left: 50%;
  width: 1920px;
  height: 100%;
  object-fit: cover;
  transform: translateX(-50%);
  z-index: 2;
}
.advanced-block-preview .preview-content {
  position: relative;
  display: block;
  width: 100%;
  max-width: calc(100% - 340px);
  z-index: 10;
}
.advanced-block-preview .preview-content .h1 {
  display: block;
  width: 100%;
  margin: 0 0 8px 0;
  font-family: "Montserrat", sans-serif;
  font-weight: 600;
  font-size: 26px;
  line-height: 1.4;
  color: #ffffff;
}
.advanced-block-preview .preview-content p {
  display: block;
  width: 100%;
  margin: 0 0 24px 0;
  font-family: "Montserrat", sans-serif;
  font-weight: 500;
  font-size: 16px;
  line-height: 1.4;
  color: #ffffff;
}
.advanced-block-preview .preview-content .sub_title {
  margin-bottom: 24px;
  font-family: "Montserrat", sans-serif;
  font-weight: 500;
  font-size: 18px;
  line-height: 1.4;
  color: #ffffff;
}
.advanced-block-preview .preview-content .divider {
  display: block;
  width: 100%;
  height: 2px;
  margin-block: 24px;
  background-color: #ffffff;
}
.advanced-block-preview .preview-content .descr {
  font-size: 15px;
}
.advanced-block-preview .wpcf7 {
  position: relative;
  display: block;
  width: 100%;
  max-width: 310px;
  padding: 24px;
  background-color: #eff1f3;
  border-radius: 16px;
  z-index: 10;
}

@container main-container (max-width: 700px) {
  .advanced-block-preview {
    max-width: 100%;
    margin-bottom: 40px;
  }
  .advanced-block-preview .preview-content {
    max-width: calc(100% - 280px);
  }
  .advanced-block-preview .wpcf7 {
    max-width: 260px;
  }
}
.mgu-advantages {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: stretch;
  width: 100%;
  padding: 40px 24px;
  container: mgu-advantages/inline-size;
}
.mgu-advantages .repeater-items {
  display: flex;
  flex-wrap: wrap;
  gap: 12px;
  margin-bottom: 24px;
}
.mgu-advantages .repeater-items .repeater-item {
  flex: 1 1 calc(32% - 10px);
  min-width: 210px;
  padding: 12px;
  background-color: #ffffff;
  box-sizing: border-box;
  border: 2px dashed #ccc;
  border-radius: 12px;
}
.mgu-advantages .repeater-items .repeater-item .repeater-item-controls {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-end;
  align-items: center;
  width: 100%;
  margin-bottom: 16px;
}
.mgu-advantages .repeater-items .repeater-item .repeater-item-controls .components-button[disabled] {
  opacity: 0.4;
}
.mgu-advantages .repeater-items .repeater-item .repeater-item-controls .controls-arrow {
  background-color: #fff;
  transition: all 0.4s ease;
}
.mgu-advantages .repeater-items .repeater-item .repeater-item-controls .controls-arrow:hover {
  background-color: rgba(0, 124, 186, 0.25);
}
.mgu-advantages .repeater-items .repeater-item .repeater-item-controls .components-button.is-destructive {
  color: #ffffff;
  background-color: #b90707;
  border: 1px solid #b90707;
  border-radius: 3px;
  transition: all 0.4s ease;
}
.mgu-advantages .repeater-items .repeater-item .repeater-item-controls .components-button.is-destructive:hover {
  background-color: transparent;
}
.mgu-advantages .repeater-items .repeater-item .repeater-image {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: flex-end;
  width: 100%;
  margin-bottom: 16px;
}
.mgu-advantages .repeater-items .repeater-item .repeater-image .add-icon {
  padding-block: 2px;
  color: #333333;
  background-color: rgba(0, 124, 186, 0.25);
  border: 1px solid rgba(0, 124, 186, 0.25);
  border-radius: 3px;
}
.mgu-advantages .repeater-items .repeater-item .repeater-image .add-icon:hover {
  background-color: transparent;
}
.mgu-advantages .repeater-items .repeater-item .repeater-image .edit-icon,
.mgu-advantages .repeater-items .repeater-item .repeater-image .delete-icon {
  position: absolute;
  display: block;
  width: 36px;
  height: 36px;
  padding: 0;
  background-color: #fff;
  transition: all 0.4s ease;
}
.mgu-advantages .repeater-items .repeater-item .repeater-image .edit-icon:hover,
.mgu-advantages .repeater-items .repeater-item .repeater-image .delete-icon:hover {
  background-color: rgba(0, 124, 186, 0.25);
}
.mgu-advantages .repeater-items .repeater-item .repeater-image .edit-icon {
  right: 38px;
  bottom: 0;
}
.mgu-advantages .repeater-items .repeater-item .repeater-image .delete-icon {
  right: 0;
  bottom: 0;
}
.mgu-advantages .repeater-items .repeater-item [role=textbox] {
  font-size: 14px;
  text-align: center;
}
.mgu-advantages .container {
  display: flex;
  flex-wrap: wrap;
  gap: 16px;
}
.mgu-advantages .block-title {
  margin-bottom: 8px;
}
.mgu-advantages .advantages-item {
  flex: 1 1 calc(33% - 20px);
  padding: 16px;
  border-radius: 16px;
  background-color: var(--white-color);
}
.mgu-advantages .advantages-item .advantages-item__icon {
  display: block;
  width: 64px;
  height: 64px;
  margin: 0 auto 8px auto;
  object-fit: contain;
}
.mgu-advantages .advantages-item .advantages-item__content {
  text-align: center;
}
.mgu-advantages .advantages-item .advantages-item__content strong {
  display: block;
  width: 100%;
  margin-bottom: 8px;
  font-size: 20px;
}
.mgu-advantages .advantages-item .advantages-item__content strong + br {
  display: none;
}

@container mgu-advantages (max-width: 991px) {
  .mgu-advantages .advantages-item {
    flex: 1 1 calc(50% - 8px);
  }
}
@container mgu-advantages (max-width: 576px) {
  .mgu-advantages .advantages-item {
    flex: 1 1 100%;
  }
}
.development {
